import { Product } from "src/@types/types";

//@Types
export const ADD_ITEM = "ADD_ITEM" as const; //  
export const DELETE_ITEM = "DELETE_ITEM" as const; //  
export const CLEAR_ITEM = "CLEAR_ITEM" as const; //   
export const INIT_ITEM = "INIT_ITEM" as const; //  localstorage     ?
export const INIT_PRICE = "INIT_PRICE" as const; //   localstorage 

//@Action
export const addCart = (product: Product) => ({ type: ADD_ITEM, product });
export const deleteCart = (product: Product) => ({ type: DELETE_ITEM, product });
export const clearCart = () => ({ type: CLEAR_ITEM });
export const initCart = () => ({ type: INIT_ITEM });
export const initPrice = () => ({ type: INIT_PRICE });
